import React from 'react'
import { Link, graphql } from 'gatsby'

import Bio from '../components/Bio'
import Layout from '../components/Layout'
import styled from 'styled-components'
import {
  Box,
  Tab,
  Tabs,
  Text,
  Heading,
  Paragraph,
} from 'grommet'

const CardLink = styled(Link)`
  :hover {
    opacity: 0.8;
  }
  text-decoration: none;
`

class BlogIndex extends React.Component {
  render() {
    const { data } = this.props

    const siteTitle =
      data.site.siteMetadata.title

    const posts =
      data.allMarkdownRemark.edges

    let codeSamples =
      data.allSitePage.edges

    codeSamples = codeSamples.filter(
      ({ node }) => {
        if (node.pluginCreator) {
          return (
            node.pluginCreator.name ===
            'gatsby-plugin-page-creator'
          )
        }
        return false
      }
    )
    return (
      <Layout
        location={this.props.location}
        title={siteTitle}
      >
        <Bio />
        <Box background="green" fill>
          <Tabs>
            <Tab title="Code">
              <Box
                fill
                align="center"
                background="accent-1"
              >
                {codeSamples.map(
                  ({ node }) => {
                    return (
                      <Text
                        size="medium"
                        margin="xsmall"
                      >
                        <Link
                          to={node.path}
                        >
                          {node.path}
                        </Link>
                      </Text>
                    )
                  }
                )}
              </Box>
            </Tab>
            <Tab title="Blog">
              <Box background="accent-2">
                {posts.map(
                  ({ node }) => {
                    const title =
                      node.frontmatter
                        .title ||
                      node.fields.slug
                    return (
                      <Box
                        fill
                        align="center"
                        key={
                          node.fields
                            .slug
                        }
                      >
                        <CardLink
                          to={
                            node.fields
                              .slug
                          }
                        >
                          <Heading level="3">
                            {title}
                          </Heading>
                          <Text size="xsmall">
                            {
                              node
                                .frontmatter
                                .date
                            }
                          </Text>
                          <Paragraph
                            dangerouslySetInnerHTML={{
                              __html:
                                node.excerpt,
                            }}
                          />
                        </CardLink>
                      </Box>
                    )
                  }
                )}
              </Box>
            </Tab>
          </Tabs>
        </Box>
      </Layout>
    )
  }
}

export default BlogIndex

export const pageQuery = graphql`
  query {
    site {
      siteMetadata {
        title
      }
    }
    allFile {
      edges {
        node {
          relativePath
        }
      }
    }
    allSitePage {
      edges {
        node {
          path
          pluginCreator {
            name
          }
        }
      }
    }
    allMarkdownRemark(
      sort: {
        fields: [frontmatter___date]
        order: DESC
      }
    ) {
      edges {
        node {
          excerpt
          fields {
            slug
          }
          frontmatter {
            date(
              formatString: "MMMM DD, YYYY"
            )
            title
          }
        }
      }
    }
  }
`
